<template>
  <div>
    <!-- 点击第一个标签不高亮的原因, indexBar以上的元素高度必须为整数的 px 单位, 带小数点就会出现偏差, 可能是内部取整了, 不修改了以示错误 -->
    <van-nav-bar title="选择城市" left-text="关闭" @click-left="quit" />
    <div class="wrap">
      <form action="/">
        <van-search
          v-model="value"
          show-action
          placeholder="输入城市"
          shape="round"
        />
      </form>
      <div class="localCityWrap CityWrap">
        <p>定位城市</p>
        <div>
            <div>{{ LocationCity }}</div>
        </div>
      </div>
      <div class="historyCityWrap CityWrap">
        <p>历史访问城市</p>
        <div>
            <div>{{ LocationCity }}</div>
            <div>上海</div>
        </div>
      </div>
      
      <baidu-map @ready="city"></baidu-map>
      <van-index-bar :index-list="firstNameList" @select="citySelected" highlight-color="#F1A363">
          <van-index-anchor index="定位历史热门" style="opacity: 0;line-height: 0px;"></van-index-anchor>
            <div class="hotCityWrap CityWrap">
                <p>热门城市</p>
                <div>
                    <div v-for="city in hotCity" :key="city.id" @click="cityClicked(city.id)" :class="currentId === city.id ? 'change' : ''">{{city.name}}</div>
                </div>
            </div>
        <div v-for="(item, index) in cityList" :key="index">
          <van-index-anchor :index="firstNameList[index]" v-if="index !== 0"></van-index-anchor>
          <van-cell
            :border="false"
            :title="item2.name"
            v-for="item2 in item"
            :key="item2.id"
          />
        </div>
      </van-index-bar>
    </div>
  </div>
</template>

<script>
import cityData from "@/assets/cityData.json";
export default {
  data() {
    return {
      // city: '',
      center: { lng: 0, lat: 0 },
      zoom: 20,
      LocationCity: "正在定位",
      cityList: [],
      firstNameList: [],
      hotCity: [],
      value: "",
      currentId: ''
    }
  },
  methods: {
    city() {
      //定义获取城市方法
      const geolocation = new BMap.Geolocation();
      var _this = this;
      geolocation.getCurrentPosition(
        function getinfo(position) {
          let city = position.address.city; //获取城市信息
          let province = position.address.province; //获取省份信息
          _this.LocationCity = city.replace(/市/g, "");
        },
        function(e) {
          _this.LocationCity = "定位失败";
        },
        { provider: "baidu" }
      )
    },
    citySelected(index) {
        console.log(index)
    },
    quit() {
      this.$router.go(-1)
    },
    cityClicked(id) {
        this.currentId = id
    }
  },
  created() {
    console.log(cityData);
    for (const key of Object.keys(cityData).sort()) {
      if (key !== "hot") {
        this.cityList.push(cityData[key]);
        this.firstNameList.push(key);
      } else {
        this.cityList.unshift({});
        // this.cityList.unshift({});
        this.firstNameList.unshift('定位历史热门');
        // this.firstNameList.shift();

        // this.firstNameList.unshift('');
        console.log(cityData[key])
        this.hotCity = cityData[key]
      }

    }
    console.log(this.cityList);
    console.log(this.firstNameList);
    console.log(this.hotCity);
    // 控制title
    this.$store.state.currentPage = this.$router.currentRoute.fullPath
  },
  mounted() {

  },
};
</script>

<style scoped lang="stylus">
>>> .van-field__body
  margin-left -10px
