    border: 4px solid #F9C34A !important
.wrap
    margin-left 40px
    margin-right 38px
.CityWrap
    margin-top 44px
    color #F7F7F7
    font-size 24px
    p
        color #828282
        font-size 24px
    div
        display flex
        flex-wrap wrap
        div
            height 60px
            width 180px
            display: flex
            align-items: center
            justify-content: center
            margin-top 27px
            margin-right 28px
            background-color #33363D
            border: 4px solid transparent
            &:first-child
                border: 4px solid #F9C34A
                background-color transparent

.hotCityWrap
    margin-bottom 52px
    div
        div
            border: 4px solid transparent
            &:first-child
                border: 4px solid transparent
                background-color #33363D
